h3 {
  color: #252525;
  font-weight: 450;
  margin: 10px 0;
}

p {
  margin: 10px 0;
  color: #9d9c9c;
  font-size: 16px;
}

.product_list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 10px;
}

.product_list img {
  width: 100%;
}

.product_list img:hover {
  opacity: 0.5 !important;
  transition: 0.5s all;
  cursor: pointer;
}

.product_name {
  font-style: italic;
  font-size: 18px;
  text-align: center;
  color: #252525;
}

.price {
  font-size: 16px;
  text-align: center;
  color: #9d9c9c;
}

.infoplus {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 150px;
  margin-top: 30px;
  background-color: #f8f9fa;
}

.form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form input,
.form button {
  padding: 10px 3px;
}

.form button {
  background-color: #383838;
  color: white;
}

.popup {
  /* width: 70%; */
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.popup > div {
  scroll-margin-inline: 20px;
}

.popup img {
  width: 100%;
}
.popup button {
  background-color: #111111;
  color: white;
  padding: 5px;
}
