.cart {
  display: grid;
  grid-template-columns: 5fr 2fr;
  grid-template-rows: 1fr;
  gap: 10px;
}

.table thead {
  font-size: 16px;
  font-weight: 400;
  background-color: #f4f5f9;
  font-style: italic;
}

.table tbody td img {
  width: 100%;
}

.table tbody td input {
  width: 70%;
}

.table tbody td button {
  border: none;
  background-color: white;
}

.table tbody td {
  text-align: center;
}

.cartTotal {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 16px;
  background-color: #f4f5f9;
}

.cartTotal h3 {
  margin-top: 0;
  text-align: center;
}

.cartTotal div {
  display: flex;
  justify-content: space-between;
}

.subtotal {
  border-bottom: 1px solid #111;
  padding: 6px 0;
}

.cartTotal div span,
.cartTotal div p,
.cartTotal div h4 {
  margin: 0;
}

.cartTotal div span {
  font-style: italic;
}

.cartTotal form input,
.cartTotal form button {
  width: 100%;
  height: 30px;
}

.cartTotal form input {
  padding: 0;
  outline: none;
  /* border: none; */
}

.cartTotal form button {
  background-color: #111;
  color: white;
}

.cartLink {
  display: flex;
  justify-content: space-between;
  background-color: #f4f5f9;
  padding: 20px;
}

.cartLink button {
  border: none;
  background-color: #f4f5f9;
  cursor: pointer;
}

.cartLink button:hover {
  color: orange;
}
