.sideBar {
  display: flex;
  flex-direction: column;
  padding-left: 15px;
}

.sideBar h3 {
  font-size: 24px;
  font-style: italic;
}

.sideBar div {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 500px;
  text-align: left;
  font-style: italic;
}

.sideBar div p {
  background-color: #f4f5f9;
  color: #252525;
  font-size: 18px;
}

.sideBar div ul li a {
  font-size: 18px;
  color: #9d9c9c;
}

.sideBar a:hover {
  color: #fac800 !important;
  padding-bottom: 0.25rem;
  transition: 0.05s all;
}
