.nav {
  width: 70%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-content: space-between;
}

.nav_back {
  justify-content: end;
}

ul {
  padding: 0px;
}

.nav ul {
  display: flex;
  gap: 20px;
}

.nav ul li {
  text-decoration: none;
  list-style: none;
}
.nav ul li a {
  text-decoration: none;
  font-weight: 500;
  color: #192d5a;
}

.nav ul li a.active {
  color: orangered;
}

h2 {
  text-align: center;
  color: #192d5a;
}

i {
  width: 10px;
  height: 10px;
  padding: 5px;
}

.btn {
  border: none;
  color: #192d5a;
  padding: 5px 10px;
  align-self: center;
  border-radius: 10px;
  background-color: #fff;
}

.btn:hover {
  color: #000;
  cursor: pointer;
  background-color: #aaa;
}

.message {
  position: fixed;
  bottom: 80px;
  right: 80px;
}
.message i {
  font-size: 30px;
  color: blue;
}

.message i:hover {
  cursor: pointer;
}

.messageTab {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}

.messageTab_top {
  display: flex;
  justify-content: space-between;
}

.messageTab_top span {
  font-weight: 600;
}

.messageTab_top button {
  background-color: #e4e7ea;
  color: #61656c;
  font-style: italic;
  border: none;
}

.messageTab_content {
  height: 250px;
}

.messageTab_content_customer {
  display: flex;
  flex-direction: column;
  align-items: end;
}

.messageTab_content_customer > p {
  width: max-content;
  display: inline-block;
  padding: 3px;
  background-color: #48b0f7;
  color: #9dcdf9;
}

.messageTab_content {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.messageTab_content_admin > section {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.messageTab_content_admin > section > p {
  margin: 0;
  display: inline-block;
  width: max-content;
  background-color: #f5f6f7;
}

.messageTab_bottom {
  display: flex;
  gap: 10px;
}
