footer {
  background-color: #111;
  margin-bottom: 0;
  margin-top: 30px;
}

.footer {
  width: 70%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  color: white;
}
.footer > div > ul > li {
  list-style: none;
}

.footer > div > ul {
  padding: 0;
}

.footer > div > h4 {
  font-size: 18px;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: white;
  font-size: 14px;
  font-weight: 300;
}
