.banner {
  margin: 0 auto;
  width: 70%;
  height: 300px;
  background-image: url("../../../public/banner.jpg");
  background-clip: border-box;
  background-size: cover;
  background-position: right top;
}

.banner_info {
  width: 50%;
  padding-left: 20px;
  padding-top: 100px;
}

.banner_info > p {
  font-size: 14px;
  font-weight: 200;
}

.banner_info > h3 {
  font-size: 22px;
  font-weight: 500;
}

.banner_info > a {
  color: #b0b5b8;
  background-color: #383838;
  padding: 10px;
}
