.h1 {
  font-size: 28px;
  font-weight: 500;
  font-style: italic;
  color: #363147;
}

.checkOut {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 10px;
}

.checkOut form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.checkOut form input {
  height: 30px;
}

.checkOut form label {
  font-size: 16px;
  font-weight: 300;
  font-style: italic;
  color: #727780;
}

.checkOut form button {
  width: 100px;
  height: 40px;
  background-color: black;
  color: white;
}

.cartTotal {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 16px;
  background-color: #f4f5f9;
}

.cartTotal h2 {
  font-size: 24px;
  font-weight: 400;
  font-style: italic;
  margin: 0;
  text-align: left;
}

.cartTotal div {
  display: flex;
  justify-content: space-between;
}

.subtotal {
  border-bottom: 1px solid #111;
  padding: 6px 0;
}

.cartTotal div span {
  font-style: italic;
}

.cartTotal div span,
.cartTotal div p,
.cartTotal div h4 {
  margin: 0;
}
