.register {
  position: relative;
  height: 450px;
  background-image: url("../../image/banner.jpg");
  background-clip: border-box;
  background-size: cover;
  background-position: right top;
}

.register h1 {
  margin: 0 auto;
  font-style: italic;
  font-weight: 400;
  color: #9d9c9c;
  margin-bottom: 30px;
}

.register form {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 30px;

  display: flex;
  flex-direction: column;

  width: 40%;
  background-color: white;
  padding: 15px;
  border-radius: 5px;
  box-shadow: 0px 3px 5px #9d9c9c4f;
}

.register form input {
  height: 30px;
  padding: 8px;
  border: 1px solid #f8f9fa;
  outline: 1px solid #9d9c9c;
}

.register form button {
  margin-top: 30px;
  padding: 10px;
  background-color: #494949;
  color: white;
}

.register form p {
  text-align: center;
}

.register form p a {
  color: blue;
}
