.detail_banner {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.detail_img {
  display: grid;
  grid-template-columns: 1fr 4fr;
  gap: 3px;
}

.detail_banner img {
  width: 100%;
}

.info h1 {
  font-style: italic;
  font-weight: 500;
}

.detail_banner button,
.description button {
  padding: 8px;
  background-color: #111111;
  color: white;
}

.description {
  margin: 30px 0;
}

.description h3,
.related h3 {
  font-style: italic;
}

.product_list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;

  gap: 10px;
}

.product_list img {
  width: 100%;
}

.product_list img:hover {
  opacity: 0.5 !important;
  transition: 0.5s all;
  cursor: pointer;
}

.product_name {
  font-style: italic;
  font-size: 18px;
  text-align: center;
  color: #252525;
}

.price {
  font-size: 16px;
  text-align: center;
  color: #9d9c9c;
}

.form {
  position: relative;
  margin-top: 30px;
}

.form label {
  position: absolute;
  padding: 12px 0;
  text-transform: uppercase;
  color: #666;
  font-size: 17px;
  margin-left: 5px;
}

.form input {
  padding: 12px 0 12px 150px;
  width: 30px;
  outline: none;
  font-size: 16px;
}

.form button {
  padding: 12px 20px;
  font-size: 16px;
  border: 1px solid #000;
  background-color: #000000d3;
  color: #fff;
  font-style: italic;
  display: inline-block;
  margin-left: 10px;
}

.form button:hover {
  cursor: pointer;
  border: 1px solid #aaa;
  background-color: #aaa;
  color: #000;
}
