.category {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.product_list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 10px;
}

.product_list img {
  width: 100%;
}

.product_list img:hover {
  opacity: 0.5 !important;
  transition: 0.5s all;
  cursor: pointer;
}

.product_name {
  font-style: italic;
  font-size: 18px;
  text-align: center;
  color: #252525;
}

.price {
  font-size: 16px;
  text-align: center;
  color: #9d9c9c;
}
