.product_tilte {
  text-align: center;
  margin-top: 50px;
}
.product_tilte > p,
.product_tilte > h3 {
  margin: 0;
}

.product_tilte > p {
  color: #c4cdd3;
  font-style: italic;
  font-size: 16px;
}

.product_tilte > h3 {
  color: #909c99;
  font-weight: 500;
}

.producttop {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 10px;
  margin-top: 30px;
}

.productbot {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 10px;
  margin-top: 10px;
}

.producttop > img,
.productbot > img {
  width: 100%;
}

.producttop > img:hover,
.productbot > img:hover {
  opacity: 0.5 !important;
  transition: 0.5s all;
  cursor: pointer;
}
