.popupOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popupContent {
  /* width: auto; */
  height: 60%;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  z-index: 99999;
  position: fixed;
  top: 10%;
  /* bottom: auto; */
  left: 30%;
  right: 30%;
}

.closeButton {
  position: absolute;
  top: 7%;
  right: 4%;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 18px;
}
