.bannerBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f4f5f9;
  height: 200px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.bannerBar h1,
.bannerBar h5 {
  padding: 0 20px;
}

.bannerBar h1 {
  color: #7e8184;
  font-style: italic;
  font-weight: 500;
}

.bannerBar h5 {
  font-size: 20px;
  font-style: italic;
  color: #9ea4a9;
  font-weight: 300;
}
